.container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.inner{
    width: 100%;
    height: 100%;
    overflow-y: auto;
    padding: 2rem;
    cursor: initial;
    position: relative;
}
.text{
    width: 100%;
    text-align: center;
}
.text h3{
    color: var(--s);
    margin-bottom: 2rem;
}
.text h3 span{
    color: var(--p);
}
.text div p{
    color: black;
    font-weight: 600;
    margin-bottom: 0.5rem;
}