.container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.inner {
  border: 1px solid rgba(0, 0, 0, 0.1);
  cursor: initial;
  background-color: white;
}
.row {
  display: flex;
}
.brt,
.brc,
.brd,
.brp {
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.brd {
  border-bottom: 3px solid var(--p);
}
.brt:last-child,
.brc:last-child,
.brd:last-child,
.brp:last-child {
  border-right: none;
}
.brf,
.brs,
.brt {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}
.brf:last-child,
.brs:last-child,
.brt:last-child,
.brl:last-child {
  border-right: none;
}
.brl {
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  border-top: 3px solid #ffc000;
}
.title {
  width: 60px;
  display: flex;
  line-height: 60px;
  padding: 1rem 0rem;
  border-top: none;
}
.title h4 {
  writing-mode: vertical-rl;
  transform: rotate(180deg);
}
.carry {
  width: 60px;
  height: 40px;
  line-height: 40px;
  text-align: center;
}
.c1 {
  color: #9dc3e6;
}
.c2 {
  color: #c5e0b4;
}
.c3 {
  color: #ffccff;
}
.digits {
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  color: #444444;
}
.digits h1 {
  font-size: 2.5rem;
  font-weight: 900;
  user-select: none;
}
.digits .cross {
  font-size: 2rem;
  font-weight: 900;
  color: var(--p);
}
