.results {
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  font-size: 1.2rem;
  font-weight: 900;
  position: relative;
}
.r1 {
  background-color: #ffccff;
  color: #7030a0;
}
.r2 {
  background-color: #e2f0d9;
  color: #00b050;
}
.r3 {
  background-color: #deebf7;
  color: #0070c0;
}
.r4 {
  background-color: #ffffff;
  color: #ffc000;
}
.frCarry {
  position: absolute;
  top: 0px;
  left: 5px;
  width: 10px;
  height: 10px;
  line-height: 20px;
  color: black;
}
.bI1 {
  color: #0070c0;
}
.bI2 {
  color: #00b050;
}
.bI3 {
  color: #7030a0;
}
@media screen and (max-width: 924px) {
  .title,
  .carry,
  .row:nth-child(1),
  .row:nth-child(2),
  .row:nth-child(3),
  .row:nth-child(4) {
    display: none;
  }
}
@media screen and (max-height: 420px) {
  .inner {
    width: 100%;
    height: 100%;
    border: none;
    background-color: transparent;
  }
  .row {
    width: 100%;
    height: 16.67%;
  }
  .digits,
  .results {
    width: 16.67%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .container {
    overflow: hidden;
  }
  .results h1,
  .digits h1 {
    font-size: 1.8rem;
  }
  .brt,
  .brc,
  .brd,
  .brp,
  .brf,
  .brs,
  .brt,
  .brl {
    border-color: white;
  }
  .brd {
    border-bottom-color: var(--p);
  }
  .brl {
    border-top-color: #ffc000;
    border-bottom: 1px solid white;
  }
  .r4 {
    background-color: transparent;
  }
}
