.header {
  width: 100%;
  height: 60px;
  box-shadow: 15px 15px 30px #c1c1c1, -15px -15px 30px #ffffff;
  padding: 0rem 3vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ico,
.logo {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}
.ico {
  width: 60px;
}
.ico i {
  color: var(--p);
  font-size: 1.5rem;
}
.logo a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.5rem 1rem;
  color: black;
  font-weight: 900;
  font-size: 1.5rem;
}
.logo a img {
  height: 30px;
}
@media screen and (max-width: 924px) {
  .header {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: none;
  }
}
@media screen and (max-height: 420px) {
  .header {
    display: none;
  }
}
