.container {
  width: 100%;
  height: 100%;
  padding-left: 27px;
}
.digits {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.digit {
  width: 26%;
  height: 18%;
  border-radius: 1rem;
  border: none;
  outline: none;
  cursor: pointer;
  font-weight: 900;
  font-size: 1.5rem;
  background: #e0e0e0;
  box-shadow: 8px 8px 15px #bebebe, -8px -8px 15px #ffffff;
}
.digit i {
  color: var(--p);
}
.digit:active {
  box-shadow: inset 10px 10px 19px #bebebe, inset -10px -10px 19px #ffffff;
}
@media screen and (max-height: 420px) {
  .container {
    padding: 1rem;
  }
}
