.container {
  width: 100%;
  height: 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
}
.inner {
  height: 100%;
  display: flex;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  overflow: hidden;
  background-color: #ffffff;
  box-shadow: 35px 35px 70px #bebebe, -35px -35px 70px #ffffff;
}
.inner button {
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;
  padding: 0rem 2rem;
  font-size: 1.2rem;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}
.inner button:hover {
  color: var(--p);
}
.inner button:disabled:hover {
  color: rgba(16, 16, 16, 0.3);
}
.inner button:last-child {
  border-right: none;
}
.inner button:active {
  color: var(--p);
  background-color: #e0e0e0;
  box-shadow: inset 8px 8px 17px #bebebe, inset -8px -8px 17px #ffffff;
}
/*Random digit modal styling*/
.randomDigitContainer {
  padding: 1rem 0rem;
}
.randomDigitContainer h1 {
  margin-bottom: 1rem;
}
.selections {
  display: flex;
  gap: 2rem;
}
.selectOption {
  cursor: pointer;
  width: 45px;
  height: 45px;
  text-align: center;
  line-height: 45px;
  font-size: 1.5rem;
  background-color: var(--s-dark);
  color: var(--s-light);
  font-weight: 900;
  text-shadow: 0 0 1px rgb(0 0 0 / 20%), 0 1px 0 rgb(0 0 0 / 20%);
  border-radius: 3px;
  transition: all 0.2s linear;
}
.selectOption:hover {
  background-color: var(--p-dark);
  color: var(--p-light);
}
.active {
  border-radius: 50%;
  background-color: var(--p-dark);
  color: var(--p-light);
}
.generate {
  width: 100%;
  padding: 1rem 0rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 450px) {
  .inner {
    width: 100%;
  }
  .inner button {
    padding: 0rem;
    width: 33%;
  }
}
